    <ul class="menu-list">
      <li>
        <a>Download the documentation</a>
      </li>
    </ul>
    <p class="menu-label has-text-centered">References</p>

    <bulma-accordion :dropdown="true">
      <bulma-accordion-item v-for="(doc, _, idx) in docs" :key="idx">
        <router-link :to="doc.href" slot="title" class="ctitle">{{
          doc.name
        }}</router-link>

        <div slot="content" v-if="doc.content">
          <ul class="menu-list content-list">
            <li
              class="inner-list"
              v-for="(content, id) in doc.content"
              :key="id"
            >
              <router-link :to="doc.href + content.anchor">
                {{ content.desc }}
              </router-link>
            </li>
          </ul>
        </div>
