<script>
import { BulmaAccordion, BulmaAccordionItem } from "vue-bulma-accordion";
import { views } from "@/views/view_docs/views.json";

export default {
  name: "Sidebar",
  data() {
    return {
      docs: Array
    };
  },
  components: {
    BulmaAccordion,
    BulmaAccordionItem
  },
  methods: {
    toggle_sidebar() {
      let $gap = document.getElementById("menugap");
      $gap.classList.toggle("is-hidden");

      let $sidebar = document.getElementById("sidebar");
      $sidebar.classList.toggle("is-hidden-mobile");
      $sidebar.classList.toggle("is-hidden-tablet-only");
      $sidebar.classList.toggle("is-hidden-desktop");
    },
  },

  created() {
    this.$eventHub.$on("t_sidebar", this.toggle_sidebar);
    this.docs = views;
  }
};
</script>

