<template>
  <div id="docs">
    <!-- page -->
    <section class="page columns is-full-height is-relative">
      <Sidebar />
      <div
        id="menugap"
        class="column is-hidden-mobile is-hidden-tablet-only is-3-desktop"
      ></div>
      <div id="doc" class="column doc is-full-height">
        <component :is="this.docView"></component>
        <Footer></Footer>
      </div>
    </section>
  </div>
</template>

<script>
// Components
import Sidebar from "@/components/docs/Sidebar.vue";
import Footer from "@/components/Footer.vue";

// Views
import _views from "@/views/view_docs/views.json";
// import { randomBytes } from 'crypto';

export default {
  name: "Docs",
  data() {
    return {
      views: null,
      docView: null,
      prevroute: '',
      i: 0
    };
  },
  methods: {
    loadComponent() {
      const docname = this.$route.params.doc.toLowerCase();
      if (docname in this.views) {
        this.docView = () => import(`@/views/view_docs/${docname}.vue`);
      } else {
        this.docView = () => import("@/views/view_docs/nf.vue");
      }

      return this.docView;
    }
  },
  created() {
    this.views = _views.views;
    this.loadComponent();
  },
  watch: {
    $route() {
      if (this.prevroute != this.$route.params.doc)
        this.loadComponent();
      this.prevroute = this.$route.params.doc
    }
  },
  components: {
    Sidebar,
    Footer
  }
};
</script>

